import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams} from 'ionic-angular';
import { DirectorProvider } from '../../providers/director/director';
import { GeocoderProvider } from '../../providers/geocoder/geocoder';
import { ProfileProvider } from '../../providers/profile/profile';
import { PopUpProvider } from '../../providers/pop-up/pop-up';
import { DirectionserviceProvider } from '../../providers/directionservice/directionservice';
declare var google;
/**
 * Generated class for the RestaurantDetailsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-restaurant-details',
  templateUrl: 'restaurant-details.html',
})
export class RestaurantDetailsPage {



  public resImage: any;
  public rate: number = 4;
  public details: string = "menus";
  public location_Name;
  public items: any;
  public categories: any;
  public phone;
  toggleMore: boolean;

  constructor(public dServ: DirectionserviceProvider, public pop: PopUpProvider, public ph: ProfileProvider, public director: DirectorProvider, public navCtrl: NavController, public navParams: NavParams) {
    this.resImage =  director.restuarant_Detail.image
    this.location_Name = director.restuarant_Detail.pos
    this.phone = director.restuarant_Detail.phone
    console.log(director.restuarant_Detail.rate)
     console.log(director.restuarant_Detail)
     let lot = new google.maps.LatLng(director.restuarant_Detail.location[0], director.restuarant_Detail.location[1])
     this.dServ.calcRoute(lot, director.restuarant_Detail.pos)
  }


  ionViewDidLoad(){
    this.pop.SmartLoader('')
    this.ph.getResturantProfile().child(this.director.restuarant_Detail.key).child('category').on('value', userProfileSnapshot => {
      this.items = [];
       console.log(userProfileSnapshot.val())
      //  this.pop.hideLoader()
      userProfileSnapshot.forEach( snap => {
        
          
          this.items.push({
            key: snap.key,
            image: snap.val().pic,
            name: snap.val().name,
          });

         
          console.log(this.items)
          return false
      
          
        
        });
    })
  }
  

  gotoFood(item){
    this.navCtrl.push('FoodDetailsPage')
    console.log(item.key)
    this.director.food_Detail = item
  }

  Accept(){
    this.navCtrl.push('BasketPage')
  }

  favorite(){
    this.pop.SmartLoader('')
    this.toggleMore = this.toggleMore ? false : true;
    if (this.toggleMore){
      this.ph.getUserProfile().child('Favorite').child(this.director.restuarant_Detail.name).update({
        image: this.director.restuarant_Detail.image,
        pos: this.director.restuarant_Detail.pos,
        phone: this.director.restuarant_Detail.phone,
        location: this.director.restuarant_Detail.location,
        name:  this.director.restuarant_Detail.name,
        openTime: this.director.restuarant_Detail.openTime,
        closeTime: this.director.restuarant_Detail.closeTime,
      }).then(()=>{
        this.ph.getResturantProfile().child(this.director.restuarant_Detail.key).update({
          fav: true
        })
        this.pop.showToast('Added To Favorites')
      })
    }else{
      this.ph.getUserProfile().child('Favorite').child(this.director.restuarant_Detail.name).remove().then(()=>{
        this.ph.getResturantProfile().child(this.director.restuarant_Detail.key).update({
          fav: false
        })
        this.pop.showToast('Removed From Favorites')
      })
    }
    
  }

  Rateme(){
    this.navCtrl.push('RatingsPage')
  }


}



